    padding-top: 80px;
    padding-bottom: 19px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    #footer-d {
        text-align: center;
        width: 100%;
      }
      
      #pagination {
        margin-bottom: 20px;
      }
      
