        margin-top: 20px;
      }

    @media (max-width: $mobile) {
        width: 320px;
        padding-top: 80px;
        padding-bottom: 19px;
        width: 320px
      }
    
      @media (min-width: $tablet) and (max-width: $desktop) {
        width: 768px;
        padding-top: 80px;
        padding-bottom: 19px;
        width: 768px
      }
    
      @media (min-width: $desktop) {
        width: 1280px;
        padding-top: 80px;
