      }
      @media (min-width: $desktop) {
        width: 100%;
      }
 }

 #footer-d {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
 }


 .tui-page-btn,
 .tui-first,
 .tui-last,
 .tui-prev,
 .tui-next {
    color: #FFFFFF;
    background-color: #000000;
    border: none;
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: 5px;
    padding-top: 5px;
 }

 .tui-page-btn:hover
 {
    border: solid 1px #DC56C5;
    border-radius: 10px 0 10px 0;
    color: #DC56C5;
 }